body {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 100px;
  margin: 0 auto;

}

.container {
  display: flex;
  flex-wrap: wrap;
}

.block {
  height: 300px;
  min-width: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  flex-basis: 33.33%;
  color: rgb(255, 255, 255);
}

@media screen and (min-width: 600px) {
  .block--3 {
    order: 4;
  }
  .block--4 {
    order: 3;
  }
  .block--5 {
    order: 5;
  }
  .block--6 {
    order: 6;
  }
}

@media screen and (min-width: 900px) {
  .block--3 {
    order: 3;
  }
  .block--4 {
    order: 6;
  }
  .block--6 {
    order: 4;
  }
}

.block--1 {
  background-color: #f00;
}

.block--2 {
  background-color: #c00;
}

.block--3 {
  background-color: #900;
}

.block--4 {
  background-color: #600;
}

.block--5 {
  background-color: #300;
}

.block--6 {
  background-color: #000;
}
